import * as React from "react";
import Timeline from "@mui/lab/Timeline";
import TimelineItem from "@mui/lab/TimelineItem";
import TimelineSeparator from "@mui/lab/TimelineSeparator";
import TimelineConnector from "@mui/lab/TimelineConnector";
import TimelineContent from "@mui/lab/TimelineContent";
import TimelineDot from "@mui/lab/TimelineDot";
import TimelineOppositeContent from "@mui/lab/TimelineOppositeContent";
import { Tooltip, Typography } from "@mui/material";
import AdsClickIcon from "@mui/icons-material/AdsClick";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  customScrollbar: {
    "&::-webkit-scrollbar": {
      width: "12px",
    },
    "&::-webkit-scrollbar-thumb": {
      backgroundColor: "#D7A766",
      boxShadow: "inset #011627 0px -80px 100px -130px",
    },
    "&::-webkit-scrollbar-track": {
      backgroundColor: "gray",
      boxShadow: "inset #011627 0px -80px 100px -85px",
    },
  },
}));

export default function ExperienceTimeLine({ jsonContent }) {
  const classess = useStyles();
  return (
    <>
      <Typography
        color="secondary"
        sx={{
          marginBottom: 4,
        }}
      >
        <AdsClickIcon />
        <div
          style={{
            fontSize: "13px",
          }}
        >
          Hover over titles for more details.
        </div>
      </Typography>
      <Timeline position="alternate">
        {jsonContent.map((item) => (
          <TimelineItem>
            <TimelineOppositeContent color="secondary">
              {item.date}
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot color="secondary" />
              <TimelineConnector
                sx={{
                  backgroundColor: "#ECC48D",
                }}
              />
            </TimelineSeparator>
            <TimelineContent color="white">
              <Tooltip
                title={
                  <div
                    style={{
                      fontSize: "14px",
                      overflow: "auto",
                      maxHeight: "70vh",
                    }}
                    className={classess.customScrollbar}
                    dangerouslySetInnerHTML={{ __html: item.description }}
                  />
                }
                arrow
                placement={!(item.id % 2) ? "right" : "left"}
              >
                <span dangerouslySetInnerHTML={{ __html: item.title }}></span>
              </Tooltip>
            </TimelineContent>
          </TimelineItem>
        ))}
      </Timeline>
    </>
  );
}
