import "./App.css";
import { CssBaseline, ThemeProvider } from "@mui/material";
import Theme from "./theme/theme";
import ToolBar from "./toolbar/toolbar";
import useCreateItem from "./generic/useCreateItem";
import ExperienceTimeLine from "./generic/timeLine";
import experienceContent from "./data/experience.json";
import educationContent from "./data/education.json";
import projectContent from "./data/projects.json";
import Content from "./generic/Content";
import image from "./images/wallpaper.png";
import SkillsSet from "./generic/SkillsSet";
import Home from "./generic/Home";
import AboutMe from "./generic/AboutMe";

function App() {
  const homeItem = useCreateItem(0, "Home", Home);
  const experienceItem = useCreateItem(
    1,
    "Experience",
    ExperienceTimeLine,
    experienceContent
  );
  const projectItem = useCreateItem(2, "Projects", Content, projectContent);
  const skillsItem = useCreateItem(3, "Skills", SkillsSet);
  const EducationItem = useCreateItem(
    4,
    "Education",
    ExperienceTimeLine,
    educationContent
  );
  const aboutMeItem = useCreateItem(5, "About me", AboutMe);

  const items = [
    homeItem,
    experienceItem,
    projectItem,
    skillsItem,
    EducationItem,
    aboutMeItem,
  ];

  return (
    <div
      style={{
        backgroundImage: `url(${image})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        height: "100vh",
      }}
    >
      <ThemeProvider theme={Theme}>
        <CssBaseline />
        <ToolBar items={items} />
      </ThemeProvider>
    </div>
  );
}

export default App;
