import * as React from "react";
import ImageList from "@mui/material/ImageList";
import ImageListItem from "@mui/material/ImageListItem";
import { makeStyles } from "@material-ui/core/styles";
import pythonImage from "../images/python.png";
import djangoImage from "../images/django.png";
import djangoRest from "../images/djangoRest.png";
import cSharp from "../images/c-sharp.png";
import dotNetCore from "../images/dotNetCore.png";
import efCore from "../images/efCore.png";
import nodeJs from "../images/nodeJs.png";
import es6 from "../images/es6.png";
import express from "../images/express.png";
import graphql from "../images/graphql.png";
import OAuth2 from "../images/OAuth2.png";
import SQLServer from "../images/SQLServer.png";
import postgre from "../images/postgre.png";
import reactjs from "../images/reactjs.png";
import angular from "../images/angular.png";
import docker from "../images/docker.png";

const useStyles = makeStyles((theme) => ({
  customScrollbar: {
    "&::-webkit-scrollbar": {
      width: "12px",
    },
    "&::-webkit-scrollbar-thumb": {
      backgroundColor: "#D7A766",
      boxShadow: "inset #011627 0px -80px 100px -130px",
    },
    "&::-webkit-scrollbar-track": {
      backgroundColor: "gray",
      boxShadow: "inset #011627 0px -80px 100px -85px",
    },
  },
}));

export default function SkillsSet() {
  const classes = useStyles();
  return (
    <ImageList
      sx={{
        width: 500,
        height: 450,
        borderRadius: "10px",
        boxShadow: "2px 2px 100px ",
      }}
      cols={3}
      rowHeight={164}
      className={classes.customScrollbar}
    >
      {itemData.map((item) => (
        <ImageListItem key={item.img}>
          <img
            srcSet={`${item.img}?w=164&h=164&fit=crop&auto=format&dpr=2 2x`}
            src={`${item.img}?w=164&h=164&fit=crop&auto=format`}
            alt={item.title}
            loading="lazy"
          />
        </ImageListItem>
      ))}
    </ImageList>
  );
}

const itemData = [
  {
    img: pythonImage,
    title: "Python",
  },
  {
    img: djangoImage,
    title: "Django",
  },
  {
    img: djangoRest,
    title: "DjangoRest",
  },
  {
    img: cSharp,
    title: "C#",
  },
  {
    img: dotNetCore,
    title: "dotNetCore",
  },
  {
    img: efCore,
    title: "efCore",
  },
  {
    img: nodeJs,
    title: "nodeJs",
  },
  {
    img: es6,
    title: "es6",
  },
  {
    img: express,
    title: "express",
  },
  {
    img: graphql,
    title: "express",
  },
  {
    img: OAuth2,
    title: "OAuth2",
  },
  {
    img: SQLServer,
    title: "SQLServer",
  },
  {
    img: postgre,
    title: "postgre",
  },
  {
    img: reactjs,
    title: "reactjs",
  },
  {
    img: angular,
    title: "angular",
  },
  {
    img: docker,
    title: "docker",
  },
];
