import React from "react";
import { Container, Typography } from "@material-ui/core";
import aboutMe from "../data/aboutme.json";

const AboutMe = () => {
  return (
    <Container
      style={{
        padding: "20px",
        borderRadius: "20px",
        boxShadow: "10px 2px 20px",
      }}
    >
      {aboutMe.map((item) => (
        <>
          <Typography
            variant="h4"
            style={{
              overflow: "auto",
              textAlign: "left",
              color: "#ECC48D",
              marginBottom: 10,
              textDecoration: "underline",
            }}
          >
            {item.title}
          </Typography>
          <Typography
            variant="body1"
            style={{
              overflow: "auto",
              textAlign: "left",
              color: "white",
              marginBottom: 30,
              opacity: 0.9,
            }}
          >
            {item.description}
          </Typography>
        </>
      ))}
    </Container>
  );
};

export default AboutMe;
