import {
  Card,
  CardContent,
  CardHeader,
  Container,
  Divider,
  Fade,
  IconButton,
  Link,
  Typography,
} from "@mui/material";
import { makeStyles } from "@material-ui/core/styles";
import { useState } from "react";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import { OpenInNew } from "@mui/icons-material";
const useStyles = makeStyles((theme) => ({
  customScrollbar: {
    "&::-webkit-scrollbar": {
      width: "12px",
    },
    "&::-webkit-scrollbar-thumb": {
      backgroundColor: "#D7A766",
      boxShadow: "inset #011627 0px -80px 100px -130px",
    },
    "&::-webkit-scrollbar-track": {
      backgroundColor: "#ECC48D",
      boxShadow: "inset #011627 0px -80px 100px -85px",
    },
  },
}));

const Content = ({ jsonContent }) => {
  const [index, setIndex] = useState(0);
  const [item, setItem] = useState(jsonContent[index]);
  const classes = useStyles();

  const changeIndexHandler = (newIndex) => {
    setIndex(newIndex);
    setItem(jsonContent[newIndex]);
  };

  return (
    <Container
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        height: "auto",
      }}
    >
      <Typography
        sx={{
          marginTop: 1,
          marginBottom: 1,
          opacity: 0.5,
        }}
        color="secondary"
      >
        {index + 1}/{jsonContent.length}
      </Typography>
      <Card
        sx={{
          width: 550,
          marginBottom: 3,
        }}
      >
        <CardHeader
          title={
            <>
              {item.title + " "}
              <Link
                href={item.link}
                target="_blank"
                rel="noopener noreferrer"
                underline="hover"
              >
                <OpenInNew fontSize="small" />
              </Link>
            </>
          }
          subheader={item.subheader}
          sx={{
            backgroundColor: "#ECC48D",
            boxShadow: "inset #011627 0px 50px 100px -100px",
          }}
        />
        <Divider sx={{ backgroundColor: "#b28900" }} />
        <CardContent
          sx={{
            boxShadow: "inset #011627 0px -80px 100px -85px",
            backgroundColor: "#ECC48D",
            height: 350,
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "flex-start",
            flexDirection: "column",
            overflow: "auto",
          }}
          className={classes.customScrollbar}
        >
          <Typography align="left" sx={{ fontSize: 17 }} color="text.secondary">
            {item.summary}
          </Typography>
          <br />
          {item.content.map((content) => (
            <>
              <Typography
                align="left"
                sx={{
                  fontSize: 15,
                  fontWeight: "bold",
                  textDecoration: "underline",
                }}
                color="text.secondary"
              >
                {content.title}
              </Typography>
              <Typography
                align="left"
                sx={{ fontSize: 13 }}
                color="text.secondary"
              >
                {content.description}
              </Typography>
              <br />
            </>
          ))}
        </CardContent>
      </Card>
      <Container
        sx={{
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
          marginBottom: 2,
        }}
      >
        <IconButton
          aria-label="delete"
          color="secondary"
          disabled={!index ? true : false}
          onClick={() => {
            changeIndexHandler(index - 1);
          }}
        >
          <ArrowBackIosIcon />
        </IconButton>
        <Divider />
        <IconButton
          aria-label="delete"
          color="secondary"
          disabled={index < jsonContent.length - 1 ? false : true}
          onClick={() => {
            changeIndexHandler(index + 1);
          }}
        >
          <ArrowForwardIosIcon />
        </IconButton>
      </Container>
    </Container>
  );
};

export default Content;
